<template>
  <div v-if="buttonSettings != ''">
    <div class="row" :class="buttonSettings" v-if="buttons != []">
      <div class="col" v-for="(button, index) in buttons"
      :key="index">
        <buttonblock :value=button></buttonblock>
      </div>
    </div>
  </div>
</template>

<script>
import extract_button_blocks from "../gutenbergparser.js";
import buttonblock from "./ButtonBlock.vue"

export default {
  mixins: [extract_button_blocks],
  components: {buttonblock},
  data() {
    return {
      buttonSettings: "",
      buttons: []
    };
  },
  props: {
    value: String
  },
  mounted() {
    let extracted = this.extract_button_blocks(this.value)
    this.buttonSettings = extracted["buttonSettings"]
    this.buttons = extracted["buttons"]
  }
};
</script>

<style scoped>
.col {
  max-width: 40%;
}


.alignleft {
  text-align: left;
}

.alignright {
  float: right;
}

.aligncenter {
  text-align: center;
}

</style>
